
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.background__image{
    z-index: -1;
    left: 0;
    top: 0;
    position: absolute;
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    opacity: .2;
    background-image: url(../public/background_seamless.jpg);
    
}
body{
    font-family: sans-serif;
    font-size: 16px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h2{
    font-weight: 700;
}
hr{
    border: 1px solid rgba(105, 105, 105, 0.2);
    border-width: 1px;
}

.container{
    display: flex;
    flex-direction: column;
    gap: 2em;
  
}

.title{
    text-align: center;
}


.question__answers{
    display: flex;
    gap: 1em;
}

.question{
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-top: 1em;
    max-width: 650px;
}


.question h3{
    text-align: center;

}

.questionsList{

   

}

.questionsList__questions{
    display: flex;
    gap: 0.5em;
    flex-direction: column;
}
button{
    border-radius: 15px;
    border: 1px solid rgba(0, 0, 0, .2);
    padding: .5em 2em;
    background-color: white;
    font-size: 14px;
}

.selected button{
  background-color: orange;
  border-color: white;
  font-weight: bolder;
  color: white;
}

.green button{
    background-color: lightgreen;
    border-color: white;
    font-weight: bolder;
    color: white;
}

.red button{
    background-color: lightcoral;
    border-color: white;
    font-weight: bolder;
    color: white;
}
.checkButton {
    align-self: center;
}

.checkButton button{
    padding: 1em 2em;

}
.checkButton button:active{
    transform: scale(.95);
    
}
